body {
    margin: 0;
    font-family: Arial, sans-serif;
}

.outer-container {
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: none;
    padding: 20px;
}

.inner-container {
    display: flex;
    flex-direction: row;
    background-color: #ccc;
    padding: 10px;
    border-radius: 5px;
    width: 50%;
}

.box {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin-right: 10px;
    border-radius: 5px;
}

.box1 {
    background-color: #ff7f7f;
}

.box2 {
    background-color: #7f7fff;
}